@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #f2f2f2;
}

.app {
    margin: 0;
    padding: 0;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    font-family: 'Oswald', sans-serif;
}

.app-header {
    background: #4267b2;
    color: #ffffff;
    text-align: center;
    min-height: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.app-title {
    margin: 0;
    padding: 0;
    text-shadow: 1px 1px 2px black;
}

.app-subtitle {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: 400;
    text-shadow: 1px 1px 2px black;
}

.app-main {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    max-width: 1600px;
    margin: 0 15px 70px 15px;
    min-width: 320px;
}

.app-footer {
    background: #4267b2;
    color: #ffffff;
    text-align: center;
    min-height: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.app-footer p {
    margin: 0;
    padding: 0;
    font-size: 1.5rem;
    font-weight: 400;
    text-shadow: 1px 1px 2px black;
}

.app-error {
    text-align: center;
    width: 100%;
    font-size: 2rem;
    color: red;
}

.load-more-btn {
    border: 2px solid #4267b2;
    padding: 17px 30px;
    cursor: pointer;
    border-radius: 1px;
    font-size: 1rem;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    transition: all 0.3s ease-in-out;
    background: transparent;
    color: #4267b2;
    margin: 30px auto auto auto;
    display: block;
}

.load-more-btn:hover {
    background: #4267b2;
    color: #ffffff ;
}

.app-not-found-page {
    text-align: center;
    font-size: 2rem;
    margin-top: 30px;
}

.form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 30px 0;
    margin: 40px 0;
}

.form__input {
    padding-top: 10px;
    outline: none;
    border: none;
    border-bottom: 2px solid #4267b2;
    width: 230px;
    background: none;
    margin-right: 10px;
    font-size: 1rem;
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    color: #333333;
    letter-spacing: 3px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus {
    box-shadow: 0 0 0 100px #f2f2f2 inset;
}


.form__button {
    border: 2px solid #4267b2;
    padding: 17px 30px;
    cursor: pointer;
    border-radius: 1px;
    font-size: 1rem;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    transition: all 0.3s ease-in-out;
    background: transparent;
    color: #4267b2;
}

.form__button:hover {
    color: #ffffff;
    background: #4267b2;
}
.recipes {
    display: -webkit-flex;
    display: flex;
    width: 80%;
    margin: auto;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.recipe {
    position: relative;
    width: 32%;
    background: #fff;
    padding-bottom: 2rem;
    border-radius: 5px;
    box-shadow: -1px 2px 34px -13px rgba(0,0,0,0.75);
    text-align: left;
    overflow: hidden;
    margin-bottom: 20px;
    height: 320px;
    min-width: 250px;
}

.recipe img {
    width: 100%;
    height: 100%;
    transition: opacity 0.15s ease-in-out;
}

.recipe img:hover {
    opacity: 0.75;
}

.recipe__title {
    text-align: center;
    color: #e74c3c;
    text-transform: uppercase;
    font-size: 1.1rem;
    padding: 10px 0;
}

.recipe__details {
    position: absolute;
    right: 0;
    padding: 10px 0;
    outline: none;
    cursor: pointer;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    transition: all 0.3s ease-in-out;
    color: #fff;
    background: #e74c3c; 
    border: 2px solid #e74c3c;
    width: 88px;
    text-decoration: none;
    text-align: center;
}

.recipe__details:hover {
    background: transparent;
    color: #e74c3c;
}

.recipe__footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255,255,255,0.85);
    padding: 10px 95px 10px 10px;
    text-transform: uppercase;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.recipe__footer p {
    letter-spacing: 3px;
}

.recipe__footer span {
    letter-spacing: normal;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    
    to {
        opacity: 1;
    }
}
    
	
.fade-in {
    -webkit-animation: fadeIn 0.7s ease-in;
            animation: fadeIn 0.7s ease-in;
}
  

@media (max-width: 1024px) {
    .recipes {
        -webkit-justify-content: space-around;
                justify-content: space-around;
    }
}
.recipe-card {
    width: 50%;
    margin: 20px auto 0 auto;
    box-shadow: -1px 2px 34px -13px rgba(0,0,0,0.75);
    padding: 30px;
    border-radius: 5px;
    overflow: hidden;
}

.recipe-card__title {
    color: #e74c3c;
    text-transform: uppercase;
    font-size: 2rem;
    padding: 0;
    margin: 15px 0 5px 0;
}

.recipe-card__img {
    width: 100%;
}

.recipe-card__publisher {
    letter-spacing: 3px;
    text-transform: uppercase;
}

.recipe-card__publisher-link {
    letter-spacing: normal;
    
}

.recipe-card__publisher span {
    letter-spacing: normal;
}

.recipe-card__button {
    padding: 10px 0;
    outline: none;
    cursor: pointer;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    transition: all 0.3s ease-in-out;
    color: #e74c3c;
    background: transparent;
    border: 2px solid #e74c3c;
    width: 88px;
    text-decoration: none;
    text-align: center;
    display: block;
}

.recipe-card__button:hover {
    color: #ffffff;
    background: #e74c3c;
}
